@import "src/styles/variables";

.title {
  text-transform: uppercase;
  font-size: 2rem;
  color: #fff;
  position: relative;
  margin-bottom: 6rem;

  &:before{
    content: "";
    bottom: -20%;
    position: absolute;
    left: 0;
    background-color: #0381ff38;
    width: 6rem;
    height: .4rem;
    border-radius: 50px;

  }
  &:after{
    content: "";
    bottom: -20%;
    position: absolute;
    left: 0;
    background-color: $blue-color;
    width: 3rem;
    height: .4rem;
    border-radius: 50px;
  }

  .title-reflection {
    color: $blue-color;
    font-size: 4rem;
    position: absolute;
    top: 55px;
    left: 4%;
    opacity: .2;
  }
}

