@import "styles/_variables";
@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&display=swap');
@import "styles/@media";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: $text-color;
  font-family: 'Comfortaa', cursive;
  font-size: 1.2rem;
  line-height: 2rem;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $night-blue-color;
    border-radius: 50px;
  }

  &::-webkit-scrollbar-track {
    background-color: #777;
  }
}

.main-content {
  margin-left: 15%;
  min-height: 100vh;
  background: linear-gradient(90deg, rgba(16, 18, 27, 1) 0%, rgba(0, 28, 70, 1) 100%);
  position: relative;
  background-size: 400% 400%;
  animation: gradient 10s ease infinite;
}

@keyframes gradient {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}


.blue {
  color: $blue-color;
}

.container {
  max-width: 1170px;
  padding: 2rem 1rem;
  margin: 0 auto;

  @media (max-width: $mobile) {
    padding: 6rem 1rem;
  }
}

.tsparticles-canvas-el {
  min-height: 100vh;
}

@media (max-width: $tablet) {
  body {
    font-size: 1rem;
  }

  .main-content {
    margin-left: 0;
  }
}