@import "src/styles/_variables";

.nav-bar {
  width: 15%;
  height: 100%;
  background-color: $blue-gray-color;
  position: fixed;
  border-right: 1px solid #2e344e;
  border-left: none;
  align-items: center;
  z-index: 15;

  &__inner {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__avatar-container {
    width: 100%;
    text-align: center;
    padding: 1rem 0;
    border-bottom: 1px solid #2e344e;
  }

  &__avatar {
    width: 85%;
    border-radius: 50%;
    border: 5px solid #2e344e;
  }

  .footer {
    border-top: inherit;
  }

  &__navigation {
    display: grid;
    width: 100%;
    text-align: center;
  }

  &__navigation-item {
    padding: .5rem 0;
    text-decoration: none;
    color: inherit;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: .4rem;
      height: 100%;
      background-color: #0381ff33;
      transform-origin: bottom;
      transform: scale(0);
      overflow-x: hidden;
      transition: transform 0.6s, 0.2s width 0.5s cubic-bezier(1, -0.16, 0.6, 1.35);
    }

    &:hover:before{
      width: 100%;
      transform: scale(1);
    }
  }

  .footer {
    padding: .5rem 0;
    border-top: 1px solid #2e344e;
    width: 100%;
    text-align: center;
  }

  .active {
    background-color: $blue-color;
  }

  &__mobile {
    width: 75px;
    height: auto;
    position: fixed;
    top: 1.5rem;
    right: 1.5rem;
    cursor: pointer;
    z-index: 15;
    backdrop-filter: blur(10px);
  }

  &__mobile-line {
    width: 100%;
    height: .4rem;
    background: $blue-color;
    z-index: 1;
    display: block;
    border-radius: 10px;

    &:not(:last-child) {
      margin-bottom: .5rem;
    }
  }
}