.achievements-page {
  &__list {
    display: grid;
    grid-template-columns: .05fr .07fr 1fr;

    &:not(:last-child) {
      margin-bottom: 1rem;
    }
  }

  &__list-item {
    font-size: 2rem;
  }

  &__list-year, &__list-item {
    text-align: center;
  }
}