@import "src/styles/_variables";
/*
* _about styles
* @author den-dev97 Khaustov D. khausden@gmail.com
* 28.02.2021
*/
.about-page {
  &__info {
    width: 48%;
  }

  &__info-text {
    margin-bottom: 1rem;
  }

  &__info-header {
    line-height: 1;
    margin-bottom: 1rem;
  }

  &__main-info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5rem;
  }

  &__header-inner {
    color: $blue-color;
  }

  &__table {
    display: grid;
    grid-template-columns: .6fr 1fr;
    margin-bottom: 1rem;
  }

  &__table-item {
    margin: 0 0 .5rem .5rem;
  }

  &__btn-container {
    text-align: center;
  }

  &__btn, &__btn-mobile {
    text-transform: uppercase;
    text-decoration: none;
    color: $text-color;
    background: linear-gradient(180deg, rgba(3,127,255,1) 0%, rgba(0,86,216,1) 50%);
    padding: 1rem 1rem;
    border-radius: 10px;
    position: relative;

    &:hover {
      background: linear-gradient(180deg, rgba(0,86,216,1) 50%, rgba(3,127,255,1) 100%);
    }

    &:after{
      position: absolute;
      content: "";
      width: 0;
      height: .3rem;
      left: 2px;
      bottom: 0;
      background-color: $text-color;;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;
      transition: all 0.4s ease-in-out;
    }
    &:hover:after{
      width: 99%;
    }
  }

  &__skills-wrapper {
    display: grid;
    grid-template-columns: .1fr 1fr;
  }

  &__skills-wrapper:not(:last-child) {
    margin-bottom: 1rem;
  }

  &__skills-item {
    font-size: 3rem;
    color: $blue-color;
  }

  &__skills-list {
    margin: 1rem 0 0 1.3rem;
  }

  &__list-item {
    list-style: circle;
  }
}