@import '../styles/title';
@import "src/styles/_variables";

.main-page {
  text-align: center;

  &__header {
    color: #fff;
    font-size: 3.2rem;
    max-width: 795px;;
    margin: 0 auto;
    padding-bottom: 1rem;
    line-height: 1;
  }

  &__header-inner {
    color: $blue-color;
  }

  &__icon-holder {
    font-size: 4rem;
    color: $text-color;
    transition: .5s;

    &:hover {
      color: $blue-color;
    }
  }

  &__wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
  }

  &__text {
    max-width: 825px;
  }

  &__text, &__header {
    backdrop-filter: blur(5px);
    border-radius: 20%;
  }
}
