* @author den-dev97 Khaustov D. khausden@gmail.com
* 05.03.2021
*/

@media (max-width: $tablet) {
  .nav-bar {
    transition: all .4s ease-in-out;
    transform: translateX(-150%);
    width: 30%;
  }

  .nav-toggle {
    transform: translateX(0);
  }

  #tsparticles {
    display: none;
  }

  .about-page {
    &__main-info {
      flex-direction: column;
    }

    &__info {
      width: 100%;
      margin-top: 4rem;
    }
  }

  .awssld {
    width: 100%;
  }

  .achievements-page {
    .title-reflection {
      font-size: 40px;
    }

    .svg-inline--fa {
      margin-right: .5rem;
    }

    &__list-year {
      margin-right: .5rem;
    }
  }
}

@media (min-width: $tablet) {
  .nav-bar__mobile {
    display: none;
  }
}

@media (min-width: $mobile) {
  .about-page__btn-mobile {
    display: none;
  }
}

@media (max-width: $mobile) {
  .about-page__btn {
    display: none;
  }

  .nav-bar {
    width: 50%;
  }

  .title {
    margin-bottom: 3rem;
  }

  .title-reflection {
    display: none;
  }

  .achievements-page, .about-page {
    .svg-inline--fa {
      margin: .5rem 1rem 0 0;
    }
  }

  .about-page {
    &__skills-list {
      margin-left: 0;
    }

    &__skills-item {
      font-size: 2rem;
    }

    &__item-other {
      display: none;
    }
  }
}